<template>
    <div class="row mt-0">
        <div class="col-10 float-left pr-1 pl-0">
            <div class="form-group">
                <input id="searchText" class="form-control" :placeholder="placeholder" type="text" autocomplete="on" v-model="searchText"> 
            </div>
        </div>
        <div class="col-2 float-left pl-0">
            <div class="background-0 text-left form-check-inline">
                <a  href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search" @click="SearchData"><em class="fa fa-search"></em></a> 
                <a id="Clear" href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear" @click="ClearData"><em class="fa fa-refresh"></em></a> 
            </div>
        </div>
    </div>       
            
</template>
<script>


export default {
    data(){
        return{            
            searchText:''
        }
    },  
    props:{
        placeholder:{
            type:String,
            required:true
        },
        GetSearchDataModuleWise:{
            type: String
        },
        GetModuleId:{
            type: Number
        }
    }, 
    methods:{
        SearchData:function(){
            // this.searchText=inputdata;
            this.$emit('SearchData',this.searchText);
        },
        ClearData:function(){ 
            
            this.searchText = '';

            if(this.GetSearchDataModuleWise != undefined){
                this.$parent.ArticleListPerModule(this.GetModuleId);
            }
            else
            {
            this.$parent.GlobalSearchList ={};
            //this.$emit('ClearData',this.searchText);
            this.$parent.IsMainDataShow = true;
        }
        }
    }
}
</script>