<script>
import DataService from '../../services/DataService';
import Search from "../../../src/views/HelpAndGuide/Search.vue";
import VamVideo from "vue-vam-video";
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
    components: { Search, VamVideo },
    name: 'VideoTutorials',


    data() {
        return {
            Module: [],
            VideoTutorialPerModuleList: [],
            IsDataNotFoundShow: false,
            PlaceHolderText: 'Search by Keyword',
            isLoading: false,
            SelectedModuleName: null,
            IsDataShow:false,
            DefaultModuleIdd: 0,
            SelectModuleIdd: '',


        }
    },

    created:async function () {
        await this.ModuleList()

        await this.VideoTutorialListPerModule(this.DefaultModuleIdd)
    },

    methods: {
        // SearchData: function (data) {
        //     var vm = this;
        //     var params = `search=${data}&userType=SA&articleType=HelpArt&moduleId=`;
        //     DataService.GetArticleList(params).then(function (response) {
        //         if (response.data.length > 0) {
        //             vm.UserManualList = response.data;
        //             vm.IsShowRow = true;
        //             vm.IsNorecordFound = false;
        //         }
        //         else if (response.data.length == 0) {
        //             vm.IsNorecordFound = true;
        //         }
        //         else {
        //             vm.IsShowRow = false;
        //             vm.IsNorecordFound = false;

        //         }
        //     });
        // },
        // ClearData: function () {
        //     window.location.reload();
        // },

        ModuleList:async function () {
            
            var vm = this;
            vm.isLoading = true;
           // var params = `moduleName=ALL&reqFrom=HelpGuide`

            await DataService.GetModulesSubModules().then(function (response) {
                
                if ((response.data != null && response.data != undefined) && (response.data.Data.length > 0 && response.data.Data != null)) {

                    response.data.Data.forEach((value, index) => {

                        let myobj = {
                            ModuleId: value.ModuleId,
                            ModuleName: value.ModuleName,

                            icon: '<em class="fa ' + value.Icon + '" aria-hidden="true"></em>'

                        }
                        vm.Module.push(myobj);
                       

                    })

                    vm.DefaultModuleIdd = vm.Module[0].ModuleId;

                    vm.isLoading = false;
                }
                else
                {
                    vm.isLoading = false;
                }
            });
        },

        VideoTutorialListPerModule:async function (Id) {
            
            var vm = this;
            vm.isLoading = true;
            var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=VideoArticle&moduleId=${Id}`

           await DataService.GetArticleList(params).then(function (response) {
                
                if (response.data != null && response.data.length > 0) {

                    vm.VideoTutorialPerModuleList = response.data;
                    vm.SelectedModuleName = vm.VideoTutorialPerModuleList[0].ModuleName;
                    vm.IsDataNotFoundShow = false;
                    vm.IsDataShow = true;
                    vm.isLoading = false;

                }
                else {
                    vm.SelectedModuleName = '';
                    vm.VideoTutorialPerModuleList = [];
                    vm.IsDataNotFoundShow = true;
                    vm.IsDataShow = false;
                    vm.isLoading = false;


                }
            });

            vm.SelectModuleIdd = Id;
        },

        embeddedUrl(URL, Type) {
            
            // Extract the video ID from the YouTube URL
            if (Type == 'UploadVideo') {
                return URL;
            }
            else {
                const videoId = this.getYouTubeId(URL);
                // Construct the embedded URL
                return `https://www.youtube.com/embed/${videoId}`;
            }


        },
        getYouTubeId(url) {
            
            // Regex to extract the YouTube video ID
            const regExp = /^.*(youtu.be\/|v\/|e\/|u\/\w+\/|embed\/|v=)([^#\&\?]*).*/;
            const match = url.match(regExp);
            if (match && match[2].length === 11) {
                return match[2];
            } else {
                return 'Invalid YouTube URL';
            }
        },

    }
}

</script>
